.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.Club-logo {
  width: 5vw;
  min-width: 50px;
  margin-left: 20px;
  margin-right: 20px;
  max-height: 99px;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.playerColor {
  color: #23ef25;
}
.roundHeader {
  color: grey;
}
.vsColor {
  color: grey;
}
.totalHeader {
  color: yellow;
}
.App-header {
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Match-body {
  min-width: 100vw;
  max-width: 100vw;
  display: flex;
	flex-wrap: wrap;
  flex-direction: row;
  align-items: baseline;
	justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  gap: 10px 100px;
}


.scoreBody {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: baseline;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  gap: 10px 30px;
  margin-top: 10px;
}
.roundBody { 
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: baseline;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  gap: 10px 30px;
  margin-top: 20px;
}

.App-body {
  min-height: 100vh;
  display: flex;
	justify-content: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.smile {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
}
.days{
  display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
}
.days > * {
  font-size: calc(10px + 2vmin);
}

.white-text{
  color: white;
}
.miip{
  margin-bottom: 56px;
}

.userinfo{
  padding-top: 10px;
}

.white-bg {
  color: white;
}

h1{
  margin-block-end: 0px;
}

.test1{
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;
	align-content: stretch;
  gap:10px;
}

.cam{
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}
.cam2{
	display: flex;
	flex-direction: row;
	justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

.testyMctest{
	display: grid;
  text-align: left;
  gap: 15px;
	grid-template-columns: 3fr 1fr 1fr;
}
.scoreBoard{
	display: grid;
	align-content: center;
  text-align: left;
  gap: 15px;
	grid-template-columns: 3fr 2fr 1fr;
}
.hihi{
  max-width: 180px;
  font-size: 0.75em;
}
.trophyLogo{
  text-align: center;
  vertical-align: middle;
  height: 20px;
}